:root {
  --font-primary: "Rubik", sans-serif;
}

:root {
  --color-bg-body: hsl(226, 43%, 10%);
  --color-bg-card: hsl(235, 46%, 20%);
  --color-report: hsl(236, 100%, 87%);
  --color-contrast: hsl(235, 45%, 61%);
  --color-bg-report: hsl(246, 80%, 60%);
  --color-bg-work: hsl(15, 100%, 70%);
  --color-bg-play: hsl(195, 74%, 62%);
  --color-bg-study: hsl(348, 100%, 68%);
  --color-bg-exercise: hsl(145, 58%, 55%);
  --color-bg-social: hsl(264, 64%, 52%);
  --color-bg-self-care: hsl(43, 84%, 65%);
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-primary);
  background-color: var(--color-bg-body);
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 2rem 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.container {
  width: 88vw;
  max-width: 68.75rem;
}
@media (min-width: 64em) {
  .container {
    display: grid;
    grid-template-columns: 260px 1fr;
    gap: 1.75rem;
  }
}

.cards {
  grid-column: 2/3;
  display: grid;
  gap: 1.75rem;
}
@media (min-width: 48em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 64em) {
  .cards {
    grid-template-columns: repeat(3, 1fr);
  }
}

.report {
  grid-column: 1/2;
  background-color: var(--color-bg-card);
  border-radius: 1rem;
  overflow: hidden;
  margin-bottom: 1.5rem;
}
@media (min-width: 64em) {
  .report {
    margin-bottom: 0;
  }
}
.report__user {
  background-color: var(--color-bg-report);
  border-radius: 1rem;
  padding: 2rem;
  display: flex;
  align-items: center;
  justify-content: initial;
  gap: 1rem;
}
@media (min-width: 64em) {
  .report__user {
    flex-direction: column;
    align-items: initial;
    gap: 2.5rem;
    padding: 2rem 2rem 5rem;
  }
}
.report__image {
  width: 4.375rem;
  height: 4.375rem;
  border-radius: 50%;
  border: 3px solid #fff;
}
@media (min-width: 48em) {
  .report__image {
    height: 5rem;
    width: 5rem;
  }
}
.report__text {
  color: #fff;
  font-size: 1.5rem;
}
@media (min-width: 48em) {
  .report__text {
    font-size: 2.5rem;
  }
}
.report__text span {
  display: block;
  color: var(--color-report);
  font-size: 1rem;
  line-height: 1.5;
}
.report__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.5rem 2rem;
}
@media (min-width: 64em) {
  .report__list {
    flex-direction: column;
    align-items: initial;
    gap: 1.5rem;
  }
}
.report__btn {
  border: none;
  background: transparent;
  color: var(--color-contrast);
  font-weight: 500;
  font-size: 1.125rem;
  cursor: pointer;
  transition: all ease 0.5s;
}
.report__btn:hover, .report__btn--active {
  color: #fff;
}

.card {
  border-radius: 1rem 1rem 1.25rem 1.25rem;
  background-repeat: no-repeat;
  background-position: 92% -1rem;
  transition: all ease 0.5s;
}
.card:hover {
  transform: scale(1.05);
}
.card:nth-child(1) {
  background-color: var(--color-bg-work);
  background-image: url(/images/icon-work.svg);
}
.card:nth-child(2) {
  background-color: var(--color-bg-play);
  background-image: url(/images/icon-play.svg);
}
.card:nth-child(3) {
  background-color: var(--color-bg-study);
  background-image: url(/images/icon-study.svg);
}
.card:nth-child(4) {
  background-color: var(--color-bg-exercise);
  background-image: url(/images/icon-exercise.svg);
  background-position: 92% -0.1rem;
}
.card:nth-child(5) {
  background-color: var(--color-bg-social);
  background-image: url(/images/icon-social.svg);
}
.card:nth-child(6) {
  background-color: var(--color-bg-self-care);
  background-image: url(/images/icon-self-care.svg);
}
.card__content {
  background-color: var(--color-bg-card);
  margin-top: 2.5rem;
  border-radius: 1rem;
  padding: 1.75rem;
  cursor: pointer;
  transition: all ease 0.5s;
}
.card__content:hover {
  background-color: #33387a;
}
.card__assignment, .card__date {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.card__date {
  margin-top: 0.5rem;
}
@media (min-width: 48em) {
  .card__date {
    flex-direction: column;
    align-items: initial;
    gap: 1rem;
    margin-top: 2rem;
  }
}
.card__title, .card__hours {
  color: #fff;
  letter-spacing: 1px;
}
.card__title {
  font-size: 1rem;
  font-weight: 500;
}
.card__hours {
  font-size: 2rem;
  font-weight: 300;
}
@media (min-width: 48em) {
  .card__hours {
    font-size: 3.125rem;
  }
}
.card__description {
  font-size: 0.95rem;
  color: var(--color-report);
}